<template>
   <div class='users'>
       <!-- <h3>用户列表组件</h3> -->
       <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>用户管理</el-breadcrumb-item>
  <el-breadcrumb-item>用户列表</el-breadcrumb-item>
</el-breadcrumb>

<!-- 卡片 -->
<el-card class="box-card">
  <!-- 搜索和添加区域 -->
  
  <el-row :gutter="25">
      <el-col :span="9"> <el-input placeholder="请输入内容" v-model="queryInfo.query" class="input-with-select" clearable @clear="getUserList">
    <el-button slot="append" icon="el-icon-search" @click=" getUserList" ></el-button>
  </el-input> 
  </el-col>
       <el-col :span="4"> 
           <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
       </el-col>
  </el-row>
  <!-- 用户列表去区域 -->
    <el-table :data="userlist" border stripe> 
          <el-table-column type="index"></el-table-column>
        <el-table-column label="姓名"  prop="username"></el-table-column>
        <el-table-column label="邮箱"  prop="email"></el-table-column>
         <el-table-column label="电话"  prop="mobile"></el-table-column>
          <el-table-column label="角色"  prop="role_name"></el-table-column>
           <el-table-column label="状态" >
               <!-- 作用域插槽 -->
               <template slot-scope="scope">
             
                  <el-switch
                    v-model="scope.row.mg_state" @change="userStateChanged(scope.row)" >
                    </el-switch>
               </template>
           </el-table-column>
            <el-table-column label="操作"  width="180px">
                <template slot-scope="scope">
                    <!-- 修改按钮 -->
                   <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                   <!-- 删除按钮 -->
                   <el-button type="danger" icon="el-icon-delete"  size="mini" @click="removeUserById(scope.row.id)"></el-button>
                   <!-- 分配角色按钮 -->
                   <el-tooltip effect="dark" content="分配角色" placement="top" :enterable="false">
                    <el-button type="warning" icon="el-icon-setting"  size="mini"></el-button>
                    </el-tooltip>
                  
                </template>
            </el-table-column>
    </el-table>
    <!-- 分页 -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page=" queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
</el-card>
<!--添加用户的 弹框 -->
<el-dialog
  title="添加用户"
  :visible.sync="addDialogVisible"
  width="50%"
  @close="addDialogClosed" 
  >
  <!-- 内容主题区域 -->
  <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px" >
      <!-- 用户名 -->
  <el-form-item label="用户名" prop="username">
    <el-input v-model="addForm.username"></el-input>
  </el-form-item>
        <!-- 密码 -->
  <el-form-item label="密码" prop="password">
    <el-input v-model="addForm.password"></el-input>
  </el-form-item>

  <el-form-item label="邮箱" prop="email">
    <el-input v-model="addForm.email"></el-input>
  </el-form-item>
  
  <el-form-item label="手机" prop="mobile">
    <el-input v-model="addForm.mobile"></el-input>
  </el-form-item>
  </el-form>
  <!-- 对话框底部的按钮区域 -->
  <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="addUser">确 定</el-button>
  </span>
</el-dialog>

    <!-- 修改用户的对话框 -->
    <el-dialog
  title="修改用户"
  :visible.sync="editDialogVisible"
  width="50%"
  @close="editDialogClosed"
  >
  <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px" >
  <el-form-item label="用户名">
    <el-input v-model=" editForm.username" disabled></el-input>
  </el-form-item>
  <el-form-item label="邮箱" prop="email">
    <el-input v-model="editForm.email" ></el-input>
  </el-form-item>
  <el-form-item label="手机号" prop="mobile">
    <el-input v-model="editForm.mobile"></el-input>
  </el-form-item>
   </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editUserInfo()">确 定</el-button>
  </span>
</el-dialog>
   </div>
</template>

<script>
export default {
   data() {
         //这是验证邮箱的规则
          var  checkEmail = (rule,value,cb)=>{
            //这是验证邮箱的正则表达式
                let regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
                if(regEmail.test(value)){
                  //合法的邮箱
                  return cb()
                }
                cb(new Error("请输入合法的邮箱"))
          }

          //验证手机号的规则
          var checkMobile =(rule,value,cb)=>{
              //这是验证手机号的正则表达式
              let regMobile =/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
              if(regMobile.test(value)){
                //合法的电话
                return  cb()
              }
              cb(new Error("请输入合法的手机号"))
          }
   //这里存放数据
       return {
            //获取用户列表的参数对象
            queryInfo:{
                query:"",
                pagenum:1, //当前的页数
                pagesize:2  //当前每页显示多少条数据
            },
            userlist:[],      //列表数据    
            total:0, //条数
            addDialogVisible:false, //控制添加对话框的显示和隐藏
            addForm:{
                username:'', //用户名
                password:'', //密码
                email:'', //邮箱
                mobile:'' //手机
            }, //添加用户的表单数据
            addFormRules:{//添加表单的验证的规则对象
                username:[
                       { required: true, message: '请输入用户名称', trigger: 'blur' }, 
                       {
                            min: 3,
                            max: 10,
                            message: '长度在 3 到 10 个字符',
                            trigger: 'blur',
                        },

                ],
                password:[
                     { required: true, message: '请输入用户密码', trigger: 'blur' }, 
                       {
                            min: 6,
                            max: 15,
                            message: '长度在 6 到 15 个字符',
                            trigger: 'blur',
                        },
                ],
                email:[
                     { required: true, message: '请输入用户邮箱', trigger: 'blur' }, 
                     {validator :checkEmail, trigger: 'blur'}
                ],
                mobile:[
                     { required: true, message: '请输入用户手机号', trigger: 'blur' }, 
                      {validator :checkMobile, trigger: 'blur'}
                ]
            }, 
            editDialogVisible:false,   //控制修改对话框的显示和隐藏
            editForm:{},  //查询到的用户信息对象
            editFormRules:{
              email:[
                 { required: true, message: '请输入用户邮箱', trigger: 'blur' },
                 {validator : checkEmail,trigger:'blur'}
              ],
              mobile:[
                 { required: true, message: '请输入用户手机号', trigger: 'blur' }, 
                  {validator :checkMobile, trigger: 'blur'}
              ]
            } //修改表单的验证规则对象
       };
   },
   //import引入的组件需要注入到对象中才能使用
   components: {

   },
   //监听属性 类似于data概念
   computed: {

   },
   //监控data中的数据变化
   watch: {

   },
   //方法集合
   methods: {
     //获取用户列表的信息
      async  getUserList(){
      let {data:res} =  await   this.$http.get("users",{
          params:this.queryInfo
      })
      if(res.meta.status!=200){
          this.$massage.error("获取用户列表失败")
      }else{
          this.userlist=res.data.users
          this.total=res.data.total
      }
      console.log(res)
        },
        //监听pagesize 改变的事件
        handleSizeChange(newSize){
            console.log(newSize)
            this.queryInfo.pagesize=newSize
            this.getUserList()  //重新获取数据
        },
        //监听页码值改变的事件
        handleCurrentChange(newPage){
                console.log(newPage)
                this.queryInfo.pagenum=newPage
               this.getUserList() //重新获取数据
        },
        //  监听 switch 开关状态的改变 修改状态，同步后台
       async userStateChanged(userInfo){
                console.log(userInfo)
          let{data:res } = await    this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`)
          if(res.meta.status!=200){
              userInfo.mg_state = !userInfo.mg_state
              this.$message.error("更新用户状态失败")
          }else{
              this.$message.success("更新用户状态成功")
          }
        },
        // 监听添加用户对话框的关闭事件,点击取消进行重置
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        //点击确定按钮添加新用户
        addUser(){
            this.$refs.addFormRef.validate(async valid =>{
                // console.log(valid)
                if(!valid) return 
                //可以发起添加用户的请求
               let {data:res} = await this.$http.post("users",this.addForm)
               console.log(res)
               if(res.meta.status !==201){
                 this.$message.error("添加用户失败")
               }else{
                 this.$message.success("添加用户成功")
                 //把弹框改成为false
                 this.addDialogVisible = false
                 //重新获取用户列表数据
                 this.getUserList()
               }
            } )
        },
        //点击修改按钮触发的事件
       async  showEditDialog(id){
        let {data:res} = await this.$http.get("users/"+id)
        console.log(res)
        if(res.meta.status!=200){
        return  this.$massage.error("查询用户信息失败")
        }
         this.editForm=res.data //把查询到的信息方到editForm中
        
            this.editDialogVisible=true
        },
        //这是监听修改用户对话框的关闭事件，点击取消进行重置
        editDialogClosed(){
            this.$refs.editFormRef.resetFields()
        },
        //这是修改用户的信息并提交
        editUserInfo(){
          this.$refs.editFormRef.validate( async valid =>{
            console.log(valid)
            if(!valid) return
            //发起修改用户信息的数据请求
             let {data:res} = await this.$http.put("users/"+ this.editForm.id,{
               email:this.editForm.email,
               mobile:this.editForm.mobile
             })
             if(res.meta.status!=200){
               return  this.$message.error("更新用户信息失败")
             }
              this.$message.success("更新用户信息成功") //提示用户修改成功
             this.editDialogVisible= false  //关闭对话框
             this.getUserList()  //更新修改后的数据
            
          })
        },
        //点击删除，根据id删除选中的用户信息
       async removeUserById(id){
            //弹框提示用户是否删除数据
           let confirmResult = await  this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)
        //如果用户确认删除，则返回值是字符串confirm 
        //如果用户取消了删除，则返回值是字符串cancel
        // console.log(confirmResult)
        if(confirmResult !== "confirm"){
          return this.$message.info("已经取消删除")
        }
            let {data:res} = await  this.$http.delete("users/"+ id)
            console.log(res)
            if(res.meta.status != 200){
               return    this.$message.error("删除失败")
            }
            this.$message.success("删除用户成功")
            this.getUserList()     //删除之后更新用户列表
        }
   },
   //生命周期 - 创建完成（可以访问当前this实例）
   created() {
        this.getUserList()
   },
   //生命周期 - 挂载完成（可以访问DOM元素）
   mounted() {

   },
   beforeCreate() {}, //生命周期 - 创建之前
   beforeMount() {}, //生命周期 - 挂载之前
   beforeUpdate() {}, //生命周期 - 更新之前
   updated() {}, //生命周期 - 更新之后
   beforeDestroy() {}, //生命周期 - 销毁之前
   destroyed() {}, //生命周期 - 销毁完成
   activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style scoped lang="less">
</style>