import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'     //1引入axios
Vue.prototype.$http = axios   //2把axios挂载到原型实例上
//3请求拦截器
axios.interceptors.request.use((config)=>{
  console.log(config)
  config.headers.Authorization = window.sessionStorage.getItem('token')
  return config
})
// axios.defaults.baseURL="http://127.0.0.1:8888/api/private/v1/" //3设置公共的请求头路径
axios.defaults.baseURL="https://www.liulongbin.top:8888/api/private/v1/" //3设置公共的请求头路径
import './plugins/element.js'   //引入element文件
import  './assets/css/global.css'  //全局样式
import './assets/fonts/iconfont.css'  //导入字体图标
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
